@keyframes lds-ripple {
	0% {
		top: 96px;
		left: 96px;
		width: 0;
		height: 0;
		opacity: 1;
	}

	100% {
		top: 18px;
		left: 18px;
		width: 156px;
		height: 156px;
		opacity: 0;
	}
}

@-webkit-keyframes lds-ripple {
	0% {
		top: 96px;
		left: 96px;
		width: 0;
		height: 0;
		opacity: 1;
	}

	100% {
		top: 18px;
		left: 18px;
		width: 156px;
		height: 156px;
		opacity: 0;
	}
}

.lds-ripple {
	position: relative;
}

.lds-ripple div {
	box-sizing: content-box;
	position: absolute;
	border-width: 4px;
	border-style: solid;
	opacity: 1;
	border-radius: 50%;
	-webkit-animation: lds-ripple 1.4s cubic-bezier(0, 0.2, 0.8, 1) infinite;
	animation: lds-ripple 1.4s cubic-bezier(0, 0.2, 0.8, 1) infinite;
}

.lds-ripple div:nth-child(1) {
	border-color: #8cd0e5;
}

.lds-ripple div:nth-child(2) {
	border-color: #376888;
	-webkit-animation-delay: -0.7s;
	animation-delay: -0.7s;
}

.lds-ripple {
	margin: 0 auto;
	width: 68px !important;
	height: 68px !important;
	-webkit-transform: translate(-34px, -34px) scale(0.34) translate(34px, 34px);
	transform: translate(-34px, -34px) scale(0.34) translate(34px, 34px);
}