.app-wrapper{
	width: 100vw;
	height: 100vh;
	display: flex;
	justify-content: center;
	align-items: center;
	transition:background .8s linear;
}

.quote-box{
	width:500px;
	padding: 40px;
	text-align: center;
	box-shadow: 6px 6px 22px -9px rgba(0,0,0,0.75);
	background:#fff;
	border-radius: 5px;
}

.author{
	margin-bottom: 20px;
	font-style: italic;
	text-align: right;
}
.text{
	font-size: 24px;
	margin-bottom: 20px;
	&__icon{
		margin-right: 10px;
	}
}